.chat{
    flex:0.65;
    display: flex;
    flex-direction:column;
}

.chat__header {
    padding:20px;
    display:flex;
    align-items:center;
    border-bottom: 1px solid lightgray;
}

.chat__headerInfo {
    flex:  1;
    padding-left: 20px;
}

.chat__header > h3 {
    margin-bottom : 30px;
    font-weight: 500;
}

.chat__headerInfo > p {
    color:gray;
}

.chat__headerRight {
    display:flex;
    justify-content:space-between;
    min-width:100px;
}

.chat__body{
    flex:1;
    background-image:url('https://user-images.githubusercontent.com/15075759/28719144-86dc0f70-73b1-11e7-911d-60d70fcded21.png');
    background-repeat: repeat;
    background-position:center;
    padding:30px;
    overflow-y: scroll;
    

}

.chat__message{
    background-color:#fff;
    box-sizing: border-box;
    padding:10px;
    width: fit-content;
    position: relative;
    font-size:16px;
    border-radius: 10px;
    margin-bottom:25px;
}

.chat__name{
    position: absolute;;
    top:-15px;
    font-weight:800;
    font-size:xx-small;

}

.chat__timestamp{
    margin-left:12px;
    font-size:xx-small;
} 

.chat__reciever{
    margin-left:auto;
    background-color: #dcf8c6;
}

.chat__footer{
    display: flex;
    justify-content: space-between;
    align-items: center;
    height:62px;
    border-top: 1px solid lightgray  ;
}
.chat__footer > form{
    flex:1;
    display:flex;
}

.chat__footer > form > input {
    flex:1;
    border-radius:30px;
    border:none;
    padding:10px;
}

.chat__footer > form > button {
    display:none;
}

.chat__footer > .MuiSvgIcon-root{
    padding: 10px;
    color: gray;
}

